

import Vue from 'vue'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'

// 承载
import idleDetail from '../pages/idleDetail.vue'
// 主页
import idleDetailIndex from '../views/idle-detail.vue'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'idleDetail',
    component: idleDetailIndex
  }]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#idleDetail',
  router,
  components: {
    idleDetail
  },
  template: '<idleDetail/>'
})



