<template>
    <div class="articleLick">
        <!-- 特效区域 -->
        <div class="unlike" ref="likeBackground" @click="this.likeArticle">
            <span class="icon-in_like_icon"></span>
        </div>
        <!-- 点赞人数 -->
        <p>
            <span>{{resData.totalLike}}</span> 人已赞
        </p>
        <!-- 点赞用户列表 -->
        <ul class="personList">
            <li v-for="(item,index) in resData.likeUserList" :key="index" @click="personalInformation(item.userId)">
                <img :src="item.userIcon">
            </li>
            <li v-if="this.resData.totalLike>6">
                <div class="manyMore">
                    +{{moreLikes}}
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
import "../assets/font-icon/style.css";
export default {
  props: ["resData"],
  data() {
    return {
      //   moreLikes: 0,
    };
  },
  computed: {
    // moreLike() {
    //   return this.likeInfo.totalLike || 0;
    // },
    moreLikes() {
      if (this.resData.totalLike > 6 && this.resData.totalLike < 1000) {
        return this.resData.totalLike - 5;
      } else {
        return 999;
      }
    }
  },
  watch: {
    // moreLike: function(newVal, oldVal) {
    //   console.log(123);
    //   if (newVal > 6 && newVal < 1000) {
    //     this.moreLikes = newVal - 5;
    //   } else {
    //     this.moreLikes = 999;
    //   }
    // }
  },
  methods: {
    personalInformation(id) {
      console.log(id);
    },
    likeArticle() {
      const likeDom = this.$refs.likeBackground;
      // this.$refs.likeBackground.classList.contains('like')
      // this.$refs.likeBackground.classList.remove("unlike");
      // this.$refs.likeBackground.classList.add("like");
      if (likeDom.classList.contains("like")) {
        this.$refs.likeBackground.classList.remove("like");
        this.$refs.likeBackground.classList.add("unlike");
      } else {
        this.$refs.likeBackground.classList.remove("unlike");
        this.$refs.likeBackground.classList.add("like");
      }
    }
  },
  mounted() {}
};
</script>
<style lang='less' scoped>
@m: 750/375;
/* 文章点赞区域 */

.container .articleLick {
  margin-bottom: 30px * @m;
}

.container .articleLick .unlike {
  position: relative;
  margin: 0 auto;
  width: 46px * @m;
  height: 46px * @m;
  background: #cfd8e2;
  box-shadow: 0 0 30px * @m 0 rgba(207, 216, 226, 0.37);
  border-radius: 100%;
  margin-bottom: 10px * @m;
}
.container .articleLick .like {
  position: relative;
  margin: 0 auto;
  width: 46px * @m;
  height: 46px * @m;
  border-radius: 100%;
  margin-bottom: 10px * @m;

  background: #fe8383;
  box-shadow: 0 0 80px 0 rgba(213, 46, 46, 0.37);
}

.container .articleLick p {
  text-align: center;
  color: #c6c6c6;
  margin-bottom: 10px * @m;
}

.container .articleLick .personList {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
}

.container .articleLick .personList li img {
  width: 36px * @m;
  height: 36px * @m;
  border-radius: 100%;
  margin-right: 6px * @m;
}

.container .articleLick .personList .manyMore {
  font-size: 14px * @m;
  width: 36px * @m;
  height: 36px * @m;
  border-radius: 100%;
  background-image: linear-gradient(45deg, #b5c1d2 0%, #e7effb 100%);
  color: #ffffff;
  letter-spacing: 0;
  text-align: center;
  line-height: 36px * @m;
}
.icon-in_like_icon {
  display: block;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -7.5px * @m;
  margin-top: -7px * @m;
}
</style>



