  width: 100px * @m;
  height: 40px * @m;
  border-radius: 100px * @m;
  text-align: center;
  line-height: 40px * @m;
  color: #b9b9b9;
  background-color: #efefef;
}

/* 标题 */

.idleTitle {
  font-size: 18px * @m;
  margin-bottom: 20px * @m;
}
